.guide-ment-wrapper {
  position: fixed;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  max-width: 600px;
  text-align: center;
  top: 60%;
  z-index: 10;
  opacity: 0;
  pointer-events: none;
  animation: guideMentShow 1s 0.5s 1 forwards, guideMentHide 1.2s 3s 1 forwards;
}
.guide-ment-wrapper .guide-ment {
  padding: 0.6rem 0.8rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background-color: rgba(0, 0, 0, 0.8);
  font-size: 1.4rem;
  font-weight: 600;
  line-height: 1.42;
  border-radius: 100px;
  color: white;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 1.2rem;
}
.guide-ment-wrapper .guide-ment .icon-left {
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-name: arrowLeftShow;
}
.guide-ment-wrapper .guide-ment .icon-right {
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-name: arrowRightShow;
}

