.splash-view {
  width: 100%;
  max-width: 600px;
  position: fixed;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  height: 100%;
  background-color: #13182c;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  pointer-events: none;
  opacity: 0;
  animation: splashShow 0.4s 1 forwards, splashHide 0.6s 1s 1 forwards;
}
