/* 서비스 둘러보기 버튼 */
#swipe-button {
  position: relative;
  background-color: #f5f5f5;
  width: 100%;
  height: 64px;
  border: white;
  border-radius: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

#swipe-button.bg-converted {
  background: white;
}

#swipe-button .slide-bg {
  background-color: #2f54eb;
  display: inline-block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  border-radius: 40px;
  opacity: 0;
}

#swipe-button .slide-text {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: default;
  font-size: 1.8rem;
  font-weight: 700;
  letter-spacing: -0.36px;
  line-height: 1.5;
  text-align: center;
  color: #595959;
  z-index: 2;
}

#swipe-circle {
  transition: width 0.3s, border-radius 0.3s, height 0.3s;
  position: absolute;
  left: 8px;
  background-color: #2f54eb;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}

#swipe-circle.unlocked {
  transition: all 0.3s;
  left: 0 !important;
  left: calc(50% - 26px) !important;
  height: 48px;
  border-radius: inherit;
}

#swipe-circle .icon {
  pointer-events: none;
}

