.period-box {
  background-color: #f5f5f5;
  border-radius: 9999px;
  padding: 0.7rem 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.7rem;
  width: 100%;
}
.period-box .period-item {
  flex: 1;
  position: relative;
  width: 100%;
}

.period-item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 0.5rem;
}
.period-item:before {
  content: "";
  background-color: #2f54eb;
  opacity: 0.2;
  display: inline-block;
  height: 1.4rem;
  width: 1px;
  position: absolute;
  top: 50%;
  left: -1.2rem;
  transform: translateY(-50%);
}
.period-item:first-child:before {
  display: none;
}
.period-item .num {
  padding: 0 0.5rem;
  height: 1.6rem;
  background-color: #2f54eb;
  border-radius: 50%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  color: white;
  font-size: 1.2rem;
  font-weight: 700;
}
.period-item .txt {
  font-size: 1.4rem;
  font-weight: 500;
  color: #262626;
  line-height: 1.3;
}

.gradient-box {
  background: linear-gradient(115deg, #13c2c2 0%, #2f54eb 100.57%);
  width: 100%;
  border-radius: 6px;
  box-shadow: 0px 4px 16px 0px rgba(0, 0, 0, 0.16);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 8rem;
}
.gradient-box .ttl {
  font-size: 1.4rem;
  font-weight: 400;
  letter-spacing: -0.28px;
}
.gradient-box .detail {
  font-size: 1.8rem;
  font-weight: 600;
  letter-spacing: -0.36px;
  line-height: 1.44;
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
}
.gradient-box .box-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 2.4rem;
  gap: 0.4rem;
  position: relative;
}
.gradient-box .box-content:before {
  content: "";
  width: 1px;
  background-color: white;
  opacity: 0.3;
  height: 3.2rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: -1px;
}
.gradient-box .box-content:first-child:before {
  display: none;
}
.gradient-box .box-content:first-child {
  padding-left: 1.6rem;
}
.gradient-box .box-content:last-child {
  padding-right: 1.6rem;
}
